@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;600&display=swap);
* {
  box-sizing: border-box;
  scroll-behavior: smooth;
  font-family: 'Poppins', sans-serif
}

body {
  min-height: 100vh;
  font-weight: 400;
  font-family: sans-serif;
}

body,
html,
.App,
#root,
.outer {
  width: 100%;
  height: 90vh;
}

.navbar-light {
  background-color: #ffffff;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
}

.outer {
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: left;
}

.inner {
  width: 450px;
  margin: auto;
  background: #ffffff;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
  padding: 40px 55px 45px 55px;
  border-radius: 15px;
  transition: all 0.3s;
}

.outer .form-control:focus {
  border-color: #167bff;
  box-shadow: none;
}

.outer h3 {
  text-align: center;
  margin: 0;
  line-height: 1;
  padding-bottom: 20px;
}

.custom-control-label {
  font-weight: 400;
}

.forgot-password,
.forgot-password a {
  text-align: right;
  font-size: 13px;
  padding-top: 10px;
  color: #7f7d7d;
  margin: 0;
}

.forgot-password a {
  color: #167bff;
}
.chapter-content{
  -ms-overflow-style: none; /* for Internet Explorer, Edge */
  scrollbar-width: none; /* for Firefox */
  overflow-y: scroll;
}
.chapter-content::-webkit-scrollbar{
  display: none;
}
.ant-list-item{
  padding: 0 !important;
  border-bottom: 0 !important;
}
.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.subnav {
  float: right;
  overflow: hidden;
}

.subnav .subnavbtn {
  font-size: 16px;
  border: none;
  outline: none;
  color: black;
  padding: 14px 16px;
  background-color: inherit;
  font-family: inherit;
  margin: 0;
}

.subnav-content {
  display: none;
  position: absolute;
  z-index: 1;
  background-color: white;
  padding: 20px;
  margin-left: -20px;
}

.subnav-content a {
  float: left;
  color: black;
  text-decoration: none;
}

.subnav:hover .subnav-content {
  display: block;
  right: 40;
}

.about__main{
    width:100%;
    background-color: #113C49;
    display: flex;
    flex-direction: column;
    padding-top: 60px;
    padding-bottom: 60px;
}

.collab__card{
    background-color: white;
    padding: 20px;
    border-radius: 20px;
}
.community_main
{
    padding-top: 60px;
    padding-bottom: 20px;
}

.community_head
{
    font-size: 48px;
    line-height: 64px;
    text-align: center;
    color: #113C49;
    font-family: Poppins;
    font-weight: bold;
}

.community_desc
{
    color: #113C49;
    text-align: center; 
    margin-top: 20px;
    margin-bottom: 40px;
}

.community_button
{
    margin: auto;
    background-color: rgb(101,218,255);
    font-family: Poppins;
    color: Black;
    font-size: 16px;
    border-radius: 100px 100px 100px 100px;
    margin-bottom: 40px;
}

.community_img
{
    object-fit: contain;
    width: 80%;
    margin-left: auto;
    margin-right: auto;
    display: block;
}
.main_feature_div{
    background-color: white;
    margin-top: 70px;
    margin-bottom: 40px;
}
.feature__card{
    border-radius:12px;
    text-align: center;
    padding: 25px;
    justify-items: center;
    align-content: center;
    min-height: 100px;
    align-items: center;
    margin: 0 10px;
}
.feature__card>p{
    color:#113C49;
    margin-top: 20px;
}

.feature__card>h4{
    color: #113C49;
    font-weight: bold;
    margin-top: 10px;
}

.feature__card>img{
    margin-bottom: 20px;
}
.bootstrap__card {
    transition: all 0.2s ease-in-out;
}

.bootstrap__card:hover {
    -webkit-transform: scale(1.02);
            transform: scale(1.02);
    border: 1px solid black !important;
    cursor: pointer;
}
.products_main {
    padding-top: 60px;
    padding-bottom: 40px;
    background-color: #113C49;
}

.products_head {
    font-size: 48px;
    line-height: 64px;
    text-align: center;
    color: white;
    font-family: Poppins;
    font-weight: bold;
}

.products_desc {
    color: white;
    text-align: center;
    margin-top: 20px;
    margin-bottom: 40px;
}

.products_button{
    margin: auto;
    background-color: rgb(101, 218, 255);
    font-family: Poppins;
    color: Black;
    font-size: 16px;
    border-radius: 100px 100px 100px 100px;
    margin-top: 40px;
}
.courses_btn{
    transition: all 2s ease-in-out;
    border: none;

}
.courses_btn:hover{
background-image: linear-gradient(to right, #167bff, #00bcd4);
}
.card {
  transition: -webkit-transform 0.3s ease-in-out;
  transition: transform 0.3s ease-in-out;
  transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
}

.wrapper {
  border-radius: 8px;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.3);
}

.wrapper:hover,
.wrapper:hover .card {
  color: white;
  background-color: #113c49;
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.card-desc {
  height: 75px;
  overflow: hidden;
}

.card-title {
  font-weight: bold;
  font-size: 20px;
  height: 50px;
}

.breadcrumb_main{
    background: none;
    padding: 0%;
}

.breadcrumb_link{
    color: white;
    text-decoration: none;
    font-weight: 600;
}

.breadcrumb_link:hover {
    text-decoration: none;
    color: #65DAFF;
}
.overlay{
        background-color: rgba(0, 0, 0, 0.3);
        width: 100%;
        height: 100%;
        position: absolute;
        padding-top: 130px;
    }

.headerTitle{
    text-underline-offset: 8px;
    font-size: 48px;
    font-family: 'Poppins', sans-serif;
    color: white;
    font-weight: 700;
}
.filterItems {
  transition: all 0.3s ease-in-out;
  border-radius: 8px;
}
.filterItems:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  cursor: pointer;
}
.filterItems:active {
  color: red;
}

.form-control {
  height: 100%;
  border: none;
  border-bottom: 2px solid #111;
  border-radius: 0px;
  margin-right: -32px;
}

.noCourse {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 50%;
}

.icon{
    transition: -webkit-transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
}
.icon:hover{
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    color: red;
cursor: pointer;
}
.icon{
    transition: color 0.3s ease-in-out,-webkit-transform;
    transition: transform,color 0.3s ease-in-out;
    transition: transform,color 0.3s ease-in-out,-webkit-transform;
}

.item-box:hover .icon{
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}

.expertbtn{
    padding-bottom: 30px;
    padding-top: 10px;
    padding-left: 20px;
    padding-right: 20px;
}

.addPost{
    background:white;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.05);
    padding: 20px;
    margin-bottom: 40px;
    transition:  0.3s ease-in-out;

}
.addPost:hover{
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
}

input,textarea{
    width: 100%;
    border-radius: 5px;
    border: 1px solid #ccc;
    padding: 0 10px;
    margin-bottom: 10px;
}
.svg-inline--fa{
    cursor: pointer;
}
.fa-trash-can{
    color: red;
    transition: -webkit-transform .2s ease-in-out;
    transition: transform .2s ease-in-out;
    transition: transform .2s ease-in-out, -webkit-transform .2s ease-in-out;
}
.fa-trash-can:hover{
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}
.productItems {
  transition: -webkit-transform 0.2s ease-in-out;
  transition: transform 0.2s ease-in-out;
  transition: transform 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;
}
.productItems:hover {
  -webkit-transform: scale(1.03);
          transform: scale(1.03);
  font-weight: 700;
  cursor: pointer;
}
.storeCard {
  transition: all 0.2s ease-in-out;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.1);
  width: 100%;
  min-height: 180px;
  border-radius: 5px;
}
.storeCard:hover {
  -webkit-transform: scale(1.02);
          transform: scale(1.02);
  cursor: pointer;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
}

