.filterItems {
  transition: all 0.3s ease-in-out;
  border-radius: 8px;
}
.filterItems:hover {
  transform: scale(1.1);
  cursor: pointer;
}
.filterItems:active {
  color: red;
}

.form-control {
  height: 100%;
  border: none;
  border-bottom: 2px solid #111;
  border-radius: 0px;
  margin-right: -32px;
}

.noCourse {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 50%;
}
